import { useCallback, useEffect } from 'react';
import { STORAGE_KEY } from '../services/StorageService';
import { logger } from '../utils';
import { StorageService } from '../services/StorageService';
import { useDispatch } from 'react-redux';

export const useAccount = () => {
  const dispatch = useDispatch();

  const fetchBiometricAuthentication = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      const infoBiometricSaved = await StorageService.get(
        STORAGE_KEY.BIOMETRIC_KEY,
      );

      if (infoBiometricSaved?.biometricKey) {
        dispatch(redux.actionContainer.setBiometricAuthentication(true));
      }
    } catch (error) {
      logger('error fetchBiometricAuthentication', error);
    }
  }, [dispatch]);

  useEffect(() => {
    fetchBiometricAuthentication();
  }, [fetchBiometricAuthentication]);
};
