import React from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import { styles } from '../styles';
import { useTranslation } from 'react-i18next';
import FastImage from 'react-native-fast-image';
import { new_user } from '../../../assets/images';
import { formatDateWithTime } from '../../../helper';
import { Review } from '../../../../../Auth/src/redux/features/network/types';
import StarsRating from '../../../components/StarRating';

interface ReviewItemProps {
  review: Review;
}

export default function ReviewItem({ review }: ReviewItemProps) {
  const { t } = useTranslation();
  const displayName =
    review?.ratedByFullName?.trim() ||
    review?.ratedByUserName ||
    t('ANONYMOUS');

  return (
    <View style={styles.reviewCard}>
      <View style={styles.reviewTopSection}>
        <View style={styles.avatarContainer}>
          <FastImage
            source={new_user}
            style={styles.avatar}
            resizeMode={FastImage.resizeMode.contain}
          />
        </View>
        <View style={styles.contentContainer}>
          <Text style={styles.reviewerName} numberOfLines={1}>
            {displayName}
          </Text>
          <StarsRating
            numberStars={5}
            rating={Math.ceil(review?.rating || 0)}
            size={16}
          />
        </View>
      </View>
      <Text style={styles.reviewComment}>{review?.comments}</Text>
      <View style={styles.dateContainer}>
        <Text style={styles.reviewDate}>
          {formatDateWithTime(review?.ratedAt || '')}
        </Text>
      </View>
    </View>
  );
}
