import { useDispatch, useSelector } from 'react-redux';

import { useAppTranslation } from '../hooks/useAppTranslation';
import { useCallback, useEffect, useState } from 'react';
import { logger } from '../utils';
import { Alert, Clipboard, Share } from 'react-native';
import { ReferralFriend } from '../types/referFriend';

export const useReferFriend = () => {
  const dispatch = useDispatch();
  const { t } = useAppTranslation();
  const [referralCode, setReferralCode] = useState('');
  const [referredFriends, setReferredFriends] = useState<ReferralFriend[]>([]);
  const { data: referralData, loading } = useSelector(
    (state: any) => state.referFriend,
  );

  const fetchGetFriendReferral = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getFriendReferralAsync());
    } catch (error) {
      logger('error fetchGetFriendReferral', error);
    }
  }, [dispatch]);

  const handleShare = async () => {
    try {
      await Share.share({
        message: 'https://tradewize.biz/registerScreen',
        title: t('SHARE_WITH_FRIENDS'),
      });
    } catch (error) {
      console.error(error);
    }
  };
  const handleCopy = () => {
    Clipboard.setString(referralCode);
    Alert.alert('Copied', t('COPIED_TO_CLIPBOARD'));
  };

  useEffect(() => {
    if (referralData) {
      setReferralCode(referralData.referralInfo?.referralCode);
      setReferredFriends(referralData.referralData);
    }
  }, [referralData]);

  useEffect(() => {
    fetchGetFriendReferral();
  }, [fetchGetFriendReferral]);

  return {
    referralCode,
    referredFriends,
    handleShare,
    handleCopy,
    loading,
  };
};
