import i18n from '../../../i18n';
import colors from '../../../theme/colors';

export const getStrengthColor = (score: number) => {
  switch (score) {
    case 4:
      return colors.green; // Mạnh
    case 3:
    case 2:
      return colors.blue; // Trung bình
    default:
      return colors.red; // Rất yếu
  }
};

export const getStrengthText = (score: number) => {
  switch (score) {
    case 4:
      return i18n?.t('strong');
    case 3:
      return i18n?.t('medium');
    case 2:
      return i18n?.t('weak');
    default:
      return i18n?.t('very_weak');
  }
};
