import React from 'react';
import { StyleSheet, View } from 'react-native';
import { SCREEN_WIDTH, Text } from 'tradewize-component';
import { colors } from '../../../theme';
import { useSelector } from 'react-redux';
import FastImage from 'react-native-fast-image';
import { new_user } from '../../../assets/images';

const Header = () => {
  const userProfile = useSelector((state: any) => state.auth.userProfile);

  return (
    <View style={styles.header}>
      <View style={styles.avatarContainer}>
        <FastImage
          source={
            userProfile?.photoUrl ? { uri: userProfile?.photoUrl } : new_user
          }
          style={styles.avatar}
        />
      </View>
      <Text style={styles.username}>{userProfile?.fullName}</Text>
    </View>
  );
};

const styles = StyleSheet.create({
  header: {
    backgroundColor: colors.mainColorPrimary,
    paddingTop: 20,
    paddingBottom: 40,
    alignItems: 'center',
    justifyContent: 'center',
  },
  avatarContainer: {
    marginBottom: 12,
  },
  avatar: {
    width: SCREEN_WIDTH * 0.2,
    height: SCREEN_WIDTH * 0.2,
    borderRadius: (SCREEN_WIDTH * 0.2) / 2,
    backgroundColor: colors.white,
  },
  username: {
    fontSize: 20,
    fontWeight: '600',
    color: colors.white,
  },
});

export default Header;
