import React, { useState } from 'react';
import { View } from 'react-native';
import { Input, Text } from 'tradewize-component';
import { styles } from './styles';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';

interface InfoRowProps {
  data: { [key: string]: string };
  isEdit?: boolean;
  handleChange?: ({ key, value }: { key: string; value: string }) => void;
}

const InfoRow = ({ data, isEdit, handleChange }: InfoRowProps) => {
  const { t } = useAppTranslation();
  const [isFocused, setIsFocused] = useState(false);

  if (isEdit) {
    const isError = data[Object.keys(data)[0]]?.length <= 0;
    return (
      <View style={styles.inputRowContainer}>
        <Input
          label={Object.keys(data)[0]}
          isRequired={true}
          labelStyle={styles.inputLabel}
          value={data[Object.keys(data)[0]]}
          inputStyle={styles.input}
          inputContainerStyle={[
            styles.inputContainer,
            isError && !isFocused && styles.inputErrorContainer,
          ]}
          onBlur={() => setIsFocused(false)}
          onFocus={() => setIsFocused(true)}
          onChangeText={text =>
            handleChange?.({ key: Object.keys(data)[0], value: text })
          }
        />
        {isError && (
          <Text style={styles.inputError}>
            {t('CANNOT_BE_EMPTY', { field: Object.keys(data)[0] })}
          </Text>
        )}
      </View>
    );
  }
  return (
    <View style={styles.infoRow}>
      <Text style={styles.label}>{Object.keys(data)[0]}</Text>
      <Text style={styles.value} numberOfLines={3}>
        {data[Object.keys(data)[0]] || '---'}
      </Text>
    </View>
  );
};

export default InfoRow;
