import { t } from 'i18next';
import React, { useState } from 'react';
import { Dimensions, StyleSheet, View } from 'react-native';
import { LineChart } from 'react-native-chart-kit';
import { formatCurrency } from '../../../utils/curencyUtil';
import { Text } from 'tradewize-component';
import colors from '../../../theme/colors';

interface PaymentTrendChartProps {
  data: {
    labels: string[];
    datasets: {
      data: number[];
    }[];
  };
}

const PaymentTrendChart: React.FC<PaymentTrendChartProps> = ({ data }) => {
  const [selectedPoint, setSelectedPoint] = useState<{
    index: number;
    x: number;
    y: number;
  } | null>(null);

  const processedData = React.useMemo(() => {
    // Convert month labels from text to numbers (1-12)
    const numericLabels = data.labels.map((label, index) =>
      (index + 1).toString(),
    );

    return {
      ...data,
      labels: numericLabels,
      datasets: data.datasets.map(dataset => ({
        ...dataset,
        data: dataset.data.map(value => (isNaN(value) ? 0 : Number(value))),
      })),
    };
  }, [data]);

  const getCurrentDate = () => {
    const date = new Date();
    return date.toLocaleDateString('en-US', {
      weekday: 'short',
      day: 'numeric',
      month: 'short',
      year: 'numeric',
    });
  };

  const formatYLabel = (yValue: string) => {
    try {
      const value = Number(yValue);
      if (isNaN(value)) return '0.00';
      return value.toFixed(2);
    } catch (error) {
      return '';
    }
  };



  if (!processedData.datasets?.[0]?.data?.length) {
    return (
      <View style={styles.container}>
        <View style={styles.chartContainer}>
          <Text style={styles.title}>
            {t('PAYMENT_TRENDS')}
          </Text>
          <Text style={styles.subtitle}>
            {getCurrentDate()}
          </Text>
        </View>
      </View>
    );
  }

  return (
    <View style={styles.container}>
      <View style={styles.chartContainer}>
        <Text style={styles.title}>
          {t('PAYMENT_TRENDS')}
        </Text>
        <Text style={styles.subtitle}>
          {getCurrentDate()}
        </Text>
        <LineChart
          data={processedData}
          width={Dimensions.get('window').width - 64}
          height={220}
          yAxisLabel=""
          yAxisSuffix=""
          formatYLabel={formatYLabel}
          chartConfig={{
            backgroundColor: colors.white,
            backgroundGradientFrom: colors.white,
            backgroundGradientTo: colors.white,
            decimalPlaces: 0,
            color: (opacity = 1) => `rgba(0, 43, 128, ${opacity})`,
            labelColor: () => colors.twColors.text.secondary,
            style: {
              borderRadius: 16,
            },
            propsForDots: {
              r: '6',
              strokeWidth: '2',
              stroke: colors.white,
            },
            propsForBackgroundLines: {
              strokeDasharray: '',
            },
          }}
          bezier
          style={{
            marginVertical: 8,
            borderRadius: 16,
          }}
          withVerticalLines={false}
          withHorizontalLines={true}
          withVerticalLabels={true}
          withHorizontalLabels={true}
          fromZero={true}
          segments={4}
          onDataPointClick={({ index, x, y }) => {
            setSelectedPoint({ index, x, y });
          }}
        />
        <Text style={styles.xAxisLabel}>{t('MONTH')}</Text>
        {selectedPoint && (
          <View
            style={[
              styles.tooltip,
              {
                left: selectedPoint.x + 5,
                top: selectedPoint.y + 50,
              },
            ]}>
            <Text
              style={styles.tooltipText}>
              {formatCurrency(
                processedData.datasets[0].data[selectedPoint.index as number],
              )}
            </Text>
          </View>
        )}
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    marginBottom: 12,
  },
  chartContainer: {
    backgroundColor: colors.white,
    borderRadius: 16,
    padding: 16,
    shadowColor: colors.black,
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.1,
    shadowRadius: 3,
    elevation: 3,
    borderWidth: 1,
    borderColor: colors.basicWhiteGray,
  },
  title: {
    fontSize: 24,
    fontWeight: '700',
    color: colors.twColors.text.black,
    marginBottom: 4,
    paddingTop: 4,
  },
  subtitle: {
    fontSize: 16,
    color: colors.mainColorPrimary,
    marginBottom: 16,
  },
  tooltip: {
    position: 'absolute',
    backgroundColor: colors.white,
    paddingVertical: 4,
    paddingHorizontal: 8,
    borderRadius: 8,
    zIndex: 1000,
  },

  tooltipText: {
    color: colors.mainColorPrimary,
    fontSize: 14,
    fontWeight: '600',
  },
  xAxisLabel: {
    fontSize: 10,
    fontWeight: '600',
    color: colors.mainColorPrimary,
    textAlign: 'right',
    marginTop: -20,
  },
});

export default PaymentTrendChart;
