import { StyleSheet, Switch, View } from 'react-native';
import { SestionCardProps } from '../type';
import React from 'react';
import { Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { IconArrowRight } from '../../../assets/svg';
import { colors } from '../../../theme';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { useSelector } from 'react-redux';

const SestionCard = (props: SestionCardProps) => {
  const { t } = useAppTranslation();
  const { section, sectionIndex } = props;

  const biometricAuthentication = useSelector(
    (state: any) => state.auth.biometricAuthentication,
  );

  return (
    <View
      style={[
        styles.sectionWrapper,
        sectionIndex === 0 && styles.sectionWrapperFirst,
      ]}
    >
      <View style={styles.sectionHeader}>
        <Text style={styles.sectionHeaderText}>{t(section.title)}</Text>
      </View>
      <View style={styles.sectionContent}>
        {section.data.map(item => (
          <View key={item.id} style={styles.menuItemContainer}>
            <Button
              disabled={item.disabled}
              variant="ghost"
              style={styles.menuItem}
              onPress={item?.onPress}
              activeOpacity={0.7}
            >
              <View style={styles.menuItemLeft}>
                <SvgXml xml={item.icon} width={24} height={24} />
                <Text style={styles.menuItemText}>{t(item.title)}</Text>
              </View>
              <View style={styles.menuItemRight}>
                {item.hasValue && (
                  <Text style={styles.menuItemValue}>{item.value}</Text>
                )}
                {item.hasSwitch && (
                  <Switch
                    value={biometricAuthentication?.data}
                    onValueChange={item?.onPress}
                    trackColor={{
                      false: colors.grayScale40,
                      true: colors.mainColorPrimary,
                    }}
                    thumbColor={colors.white}
                  />
                )}
                {item.hasArrow && (
                  <SvgXml
                    xml={IconArrowRight(colors.black)}
                    width={20}
                    height={20}
                  />
                )}
              </View>
            </Button>
          </View>
        ))}
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  sectionWrapper: {
    marginHorizontal: 16,
    marginTop: 24,
    borderRadius: 12,
    borderWidth: 1,
    borderColor: colors.grayScale30,
    backgroundColor: colors.white,
    overflow: 'hidden',
  },
  sectionWrapperFirst: {
    marginTop: -30,
    borderTopLeftRadius: 12,
    borderTopRightRadius: 12,
  },
  sectionHeader: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    paddingHorizontal: 16,
    paddingTop: 16,
    paddingBottom: 12,
    backgroundColor: colors.white,
  },
  sectionContent: {
    paddingHorizontal: 16,
    paddingBottom: 16,
  },
  sectionHeaderText: {
    fontSize: 18,
    fontWeight: '600',
    color: colors.black,
  },
  playButton: {
    width: 24,
    height: 24,
    borderRadius: 4,
    backgroundColor: colors.mainColorPrimary,
    alignItems: 'center',
    justifyContent: 'center',
  },
  menuItemContainer: {
    marginBottom: 12,
    borderRadius: 12,
    borderWidth: 1,
    borderColor: colors.grayScale30,
    backgroundColor: colors.white,
    overflow: 'hidden',
  },
  menuItem: {
    justifyContent: 'space-between',
    alignItems: 'center',
    paddingHorizontal: 16,
    paddingVertical: 10,
    backgroundColor: colors.white,
    minHeight: 0,
  },
  menuItemLeft: {
    flexDirection: 'row',
    alignItems: 'center',
    flex: 1,
  },
  menuItemText: {
    fontSize: 16,
    color: colors.black,
    marginLeft: 12,
    flex: 1,
  },
  menuItemRight: {
    flexDirection: 'row',
    alignItems: 'center',
    marginLeft: 12,
  },
  menuItemValue: {
    fontSize: 16,
    color: colors.grayScale60,
    marginRight: 12,
  },
});

export default SestionCard;
