import React from 'react';
import { View } from 'react-native';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { styles } from './styles';
import { ShadowedView } from 'react-native-fast-shadow';
import { shadowGlobal } from '../../../../types/global';
import _ from 'lodash';
import { IconClose } from '../../../../assets/svg';
import { colors } from '../../../../theme';
import { SvgXml } from 'react-native-svg';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { logger } from '../../../../utils';
import { useDispatch } from 'react-redux';
import FastImage from 'react-native-fast-image';
import { camera, uploadImage } from '../../../../assets/images';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import ImagePicker from 'react-native-image-crop-picker';
import { compressImage } from '../../../../helper';
import RNFS from 'react-native-fs';
import mime from 'mime';

enum MediaType {
  CAMERA = 'camera',
  GALLERY = 'gallery',
}

const ModalChangeAvatar = () => {
  const insets = useSafeAreaInsets();
  const dispatch = useDispatch();
  const { t } = useAppTranslation();

  const delay = (ms: number) =>
    new Promise((resolve: any) => setTimeout(resolve, ms));

  const handleClose = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.hideCustomModal());
    } catch (error) {
      logger('error handleClose', error);
    }
  };

  const handlePickMedia = async (type: MediaType) => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.hideCustomModal());
      await delay(350);

      let result: any = null;

      if (type === MediaType.GALLERY) {
        result = await ImagePicker.openPicker({
          cropping: false,
          includeExif: true,
          mediaType: 'photo',
          forceJpg: true,
          includeBase64: false,
        });
      } else {
        result = await ImagePicker.openCamera({
          cropping: false,
          multiple: false,
          mediaType: 'photo',
          forceJpg: true,
          includeBase64: false,
          includeExif: true,
        });
      }

      const compressedImage = (await compressImage(result?.path)) || '';
      const stat = await RNFS.stat(compressedImage);
      const typeFile = mime.getType(stat?.path);
      const ext = stat?.path?.split('.').pop();
      const fileName = `image_${Date.now()}.${ext}`;

      const media = {
        uri: stat?.path,
        type: typeFile,
        name: fileName,
        // size: stat?.size,
      };

      dispatch(
        redux.actionContainer.setUserAvatarAsync({
          PhotoImage: media,
        }),
      );
    } catch (error) {
      logger('error handlePickMedia', error);
    }
  };

  return (
    <View style={[styles.container, { paddingBottom: insets.bottom }]}>
      <ShadowedView style={[styles.contentContainer, shadowGlobal]}>
        <View style={styles.headerContainer}>
          <Button
            onPress={handleClose}
            variant="secondary"
            style={styles.closeButton}
          >
            <SvgXml
              xml={IconClose(colors.black)}
              width={SCREEN_WIDTH * 0.05}
              height={SCREEN_WIDTH * 0.05}
            />
          </Button>
        </View>
        <View style={styles.selectButtonContainer}>
          {_.times(2, index => (
            <Button
              onPress={() =>
                handlePickMedia(
                  index === 0 ? MediaType.CAMERA : MediaType.GALLERY,
                )
              }
              key={index}
              variant="ghost"
              style={styles.selectButtonItem}
            >
              <FastImage
                source={index === 0 ? camera : uploadImage}
                style={styles.selectButtonImage}
                tintColor={colors.mainColorPrimary}
                resizeMode="contain"
              />
              <Text style={styles.selectButtonText}>
                {index === 0 ? t('TAKE_PHOTO') : t('GALLERY')}
              </Text>
            </Button>
          ))}
        </View>
      </ShadowedView>
    </View>
  );
};

export default ModalChangeAvatar;
