import React, { useMemo } from 'react';
import { View } from 'react-native';
import DistributionBar from './DistributionBar';

interface RatingDistributionProps {
  distribution: number; // Số lượng level (ví dụ: 5)
  data: number[]; // Mảng các rating (ví dụ: [4.5, 5, 3.2, 4, 5, 2.1])
}

export default function RatingDistribution({
  distribution = 5,
  data,
}: RatingDistributionProps) {
  // Tính toán distribution từ data
  const { distributionCounts, totalReviews } = useMemo(() => {
    if (!data || data.length === 0) {
      const emptyCounts: { [key: number]: number } = {};
      for (let i = distribution; i >= 1; i--) {
        emptyCounts[i] = 0;
      }
      return {
        distributionCounts: emptyCounts,
        totalReviews: 0,
      };
    }

    const counts: { [key: number]: number } = {};

    // Khởi tạo tất cả level từ distribution xuống 1
    for (let i = distribution; i >= 1; i--) {
      counts[i] = 0;
    }

    // Đếm số lượng mỗi level (làm tròn lên)
    data.forEach(rating => {
      const roundedRating = Math.ceil(rating);
      if (roundedRating >= 1 && roundedRating <= distribution) {
        counts[roundedRating] = (counts[roundedRating] || 0) + 1;
      }
    });

    return {
      distributionCounts: counts,
      totalReviews: data.length,
    };
  }, [distribution, data]);

  // Tạo mảng từ distribution xuống 1 (ví dụ: [5, 4, 3, 2, 1])
  const levels = useMemo(() => {
    const result: number[] = [];
    for (let i = distribution; i >= 1; i--) {
      result.push(i);
    }
    return result;
  }, [distribution]);

  return (
    <View>
      {levels.map(starLevel => (
        <DistributionBar
          key={starLevel}
          starLevel={starLevel}
          count={distributionCounts[starLevel] || 0}
          totalReviews={totalReviews}
        />
      ))}
    </View>
  );
}
