import React, { useCallback, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { ScrollView, View, Image } from 'react-native';
import {
  IconChart,
  IconCalendar,
  IconArrowDown,
} from '../../assets/svg/index.ts';
import PaymentIncomeCard from './Components/PaymentCard';
import PaymentTrendChart from './Components/PaymentChart';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
// import { playInstructionalVideos } from 'src/assets/images.ts';
// import { VideoModal } from 'tradewize';
import { SvgXml } from 'react-native-svg';
import { formatCurrency } from '../../utils/curencyUtil';
import { potential, taxForm, budget } from '../../assets/images';
import { TableJob, MonthData, PaymentApiResponse } from './type';
import { styles } from './styles';
import {
  WheelMonthYearPicker,
  Text,
  HeaderBase,
  Button,
  BaseContainer,
} from 'tradewize-component';
import {
  navigationRef,
  NavigationService,
} from '../../services/NavigationService';
import { getPaymentPo } from '../../api/endpoint';
import colors from '../../theme/colors.ts';

dayjs.extend(customParseFormat);

export default function PaymentMethodScreen() {
  const { t } = useTranslation();
  const [selectedMonth, setSelectedMonth] = useState(new Date());
  const [jobsData, setJobsData] = useState<TableJob[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [showChart, setShowChart] = useState(false);
  const [paymentSummary, setPaymentSummary] =
    useState<PaymentApiResponse | null>(null);
  const [isMonthPickerVisible, setIsMonthPickerVisible] = useState(false);

  const fetchPaymentPo = useCallback(async (targetDate: Date) => {
    setIsLoading(true);
    try {
      const client = await import('auth/api');
      const month = dayjs(targetDate).month() + 1;
      const year = dayjs(targetDate).year();
      const response = await client.getRequest(
        `${getPaymentPo}?month=${month}&year=${year}`,
      );
      const payload: PaymentApiResponse | null =
        (response?.data?.data as PaymentApiResponse) ||
        (response?.data as PaymentApiResponse) ||
        null;

      if (!payload) {
        setJobsData([]);
        setPaymentSummary(null);
        return;
      }

      const mappedJobs: TableJob[] = (payload.dataTable || []).map(item => {
        const communityCode = item?.communityInfo?.code || '';
        const lotNumber = item?.houseInfo?.lotNumber || '';
        const displayId =
          communityCode && lotNumber
            ? `${communityCode}-${lotNumber}`
            : item.id;

        return {
          id: displayId,
          role: item.companyRoleName || '',
          rate: Number(item.rate) || 0,
          paymentDate: item.paymentDate || null,
          estPayDate: item.estPayDate || null,
          paymentStatus: item.paymentStatus ?? 0,
          poNumber: item.poNumber || '',
        };
      });

      setJobsData(mappedJobs);
      setPaymentSummary(payload);
    } catch (error) {
      console.error('Error fetching payment po:', error);
    } finally {
      setIsLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchPaymentPo(selectedMonth);
  }, [fetchPaymentPo, selectedMonth]);

  const chartData = React.useMemo(() => {
    if (!paymentSummary?.dataChart?.length) {
      return { labels: [], datasets: [{ data: [] }] };
    }

    const monthsLabels = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec',
    ];

    const yearData = paymentSummary.dataChart[0];
    const dataByMonth = new Array(12).fill(0);
    yearData.months.forEach((monthInfo: MonthData) => {
      try {
        const amount = Number(monthInfo.amountMonth);
        dataByMonth[monthInfo.month - 1] =
          !isNaN(amount) && isFinite(amount)
            ? Math.round(amount * 100) / 100
            : 0;
      } catch (error) {
        console.error('Error processing amount:', error);
        dataByMonth[monthInfo.month - 1] = 0;
      }
    });

    return {
      labels: monthsLabels,
      datasets: [
        {
          data: dataByMonth,
          color: (opacity = 1) => `rgba(0, 43, 128, ${opacity})`,
          strokeWidth: 2,
        },
      ],
    };
  }, [paymentSummary]);

  const handleTaxFormsPress = () => {
    // Handle Tax Forms button press
    console.log('Tax Forms pressed');
  };

  const handlePersonalBudgetPress = () => {
    NavigationService.navigate('myBudget');
  };

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        title={t('PERSONAL_INFORMATION')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />
      <View style={styles.incomeSummary}>
        <Text style={styles.summaryTitle}>{t('TOTAL_PROJECTED_INCOME')}</Text>
        <Text style={styles.totalAmount}>
          {paymentSummary?.totalProjectAmount
            ? formatCurrency(paymentSummary.totalProjectAmount)
            : '$0.00'}
        </Text>
        <Button style={styles.potentialButton}>
          <Image
            source={potential}
            style={styles.potentialIcon}
            resizeMode="contain"
            width={24}
            height={24}
          />
          <Text style={styles.potentialLabel}>{t('POTENTIAL')}: </Text>
          <Text style={styles.potentialValue}>
            {paymentSummary?.totalPotential
              ? formatCurrency(paymentSummary.totalPotential)
              : formatCurrency(0)}
          </Text>
        </Button>
      </View>

      <ScrollView
        style={styles.scrollView}
        contentContainerStyle={styles.content}
        showsVerticalScrollIndicator={false}
      >
        <View style={styles.datePickerSection}>
          <View style={styles.datePickerWrapper}>
            <Button
              style={[styles.button]}
              variant="secondary"
              onPress={() => setIsMonthPickerVisible(true)}
            >
              <SvgXml xml={IconCalendar(colors.black)} />
              <Text
                numberOfLines={1}
                style={[
                  styles.txtDate,
                  { color: selectedMonth ? colors.black : colors.grayScale70 },
                ]}
              >
                {selectedMonth
                  ? dayjs(selectedMonth).format('MM-YYYY')
                  : t('SELECT_A_DATE')}
              </Text>
              <SvgXml
                xml={IconArrowDown(colors.black)}
                style={styles.sampleArrow}
              />
            </Button>
            <WheelMonthYearPicker
              key={`${dayjs(selectedMonth).format(
                'MM-YYYY',
              )}-${isMonthPickerVisible}`}
              isVisible={isMonthPickerVisible}
              initialDate={dayjs(selectedMonth).startOf('month').toDate()}
              minDate={new Date(dayjs().year() - 5, 0)}
              maxDate={new Date(dayjs().year(), dayjs().month())}
              handleSelectedDate={(date: string) => {
                // WheelMonthYearPicker returns string "MM-YYYY"
                const raw = date?.trim?.() || '';
                const parsed = dayjs(
                  raw,
                  ['MM-YYYY', 'MM/YYYY', 'YYYY-MM', 'YYYY/MM'],
                  true,
                );
                const safe = parsed.isValid() ? parsed : dayjs(); // fallback to current date
                const parsedDate = safe.startOf('month').toDate();

                console.log('WheelMonthYearPicker selected', {
                  raw,
                  month: safe.month() + 1,
                  year: safe.year(),
                  isValid: parsed.isValid(),
                });

                setSelectedMonth(parsedDate);
                fetchPaymentPo(parsedDate);
                setIsMonthPickerVisible(false);
              }}
              styleButtonCancel={{ backgroundColor: colors.mainColorPrimary }}
              styleButtonConfirm={{ backgroundColor: colors.mainColorPrimary }}
              styleTextCancel={{ color: colors.white }}
              styleTextConfirm={{ color: colors.white }}
              onCancel={() => setIsMonthPickerVisible(false)}
            />
          </View>
          <Button
            style={styles.chartButton}
            onPress={() => setShowChart(!showChart)}
          >
            <SvgXml xml={IconChart('#002B80')} width={24} height={24} />
          </Button>
        </View>

        {showChart && <PaymentTrendChart data={chartData} />}

        {isLoading ? (
          <View style={styles.loadingContainer}>
            <Text>Loading...</Text>
          </View>
        ) : (
          <View style={styles.jobsList}>
            {jobsData.map((job, index) => (
              <PaymentIncomeCard
                key={index}
                id={job.id}
                paymentStatus={job.paymentStatus}
                role={job.role}
                amount={job.rate}
                paymentDate={job.paymentDate}
                estPayDate={job.estPayDate}
                poNumber={job?.poNumber}
              />
            ))}
          </View>
        )}
      </ScrollView>

      <View style={styles.buttonContainer}>
        <Button style={styles.actionButton} onPress={handleTaxFormsPress}>
          <Image
            source={taxForm}
            style={styles.buttonIcon}
            resizeMode="contain"
            width={24}
            height={24}
          />
          <Text style={styles.buttonText}>{t('TAX_FORMS')}</Text>
        </Button>

        <Button
          style={styles.actionButtonBudget}
          onPress={handlePersonalBudgetPress}
        >
          <Image
            source={budget}
            style={styles.buttonIcon}
            resizeMode="contain"
            width={24}
            height={24}
          />
          <View style={styles.buttonTextContainer}>
            <Text style={styles.buttonText}>{t('PERSONAL')}</Text>
            <Text style={styles.buttonText}>{t('BUDGET')}</Text>
          </View>
        </Button>
      </View>
      {/* Intro Video */}
      {/* <VideoModal
        visible={isShowVideo}
        onClose={() => setIsShowVideo(false)}
        source={
          mediaState?.['PAYMENT_ACCOUNT_SCREEN' as keyof typeof mediaState]
            ?.url || ''
        }
        subtitle={
          mediaState?.['PAYMENT_ACCOUNT_SCREEN' as keyof typeof mediaState]
            ?.subtitle || {}
        }
        autoPlay={true}
        // showSkipButton={Config.ENV !== 'PRODUCT'}
      /> */}
    </BaseContainer>
  );
}
