import React, { useState } from 'react';
import { BaseContainer, Button, HeaderBase, Text } from 'tradewize-component';
import { styles } from './styles';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { navigationRef } from '../../services/NavigationService';
import {
  ScrollView,
  View,
  TouchableOpacity,
  ActivityIndicator,
} from 'react-native';
import FastImage from 'react-native-fast-image';
import { referFriend } from '../../assets/images';
import { SvgXml } from 'react-native-svg';
import { IconCopy, IconGift, IconSend, IconUserRef } from '../../assets/svg';
import { useReferFriend } from '../../hooks/referFriend';
import { FriendCard } from './components/friendCard';
import { EmptyComponent } from '../../components/EmptyComponent';
import { colors } from '../../theme';

export default function ReferFriendScreen() {
  const { t } = useAppTranslation();
  const { referralCode, referredFriends, handleShare, handleCopy, loading } =
    useReferFriend();
  const [isFriendsCollapsed, setIsFriendsCollapsed] = useState(true);

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        title={t('REFER_FRIEND')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />
      <ScrollView
        contentContainerStyle={styles.content}
        showsVerticalScrollIndicator={false}
      >
        <View style={styles.imgContainer}>
          <FastImage source={referFriend} style={styles.referFriendImage} />
        </View>
        <Text style={styles.title}>{t('REFER_FRIEND_TITLE')}</Text>
        <View style={styles.contentContainer}>
          <Text style={styles.containerTitle}>{t('HOW_IT_WORKS')}</Text>
          <View style={styles.itemContainer}>
            <View style={styles.iconContainer}>
              <SvgXml xml={IconSend()} width={24} height={24} />
            </View>
            <View style={styles.itemContent}>
              <Text style={styles.itemContentTitle}>
                {t('SHARE_REFERRAL_CODE_DES')}
              </Text>
            </View>
          </View>
          <View style={styles.itemContainer}>
            <View style={styles.iconContainer}>
              <SvgXml xml={IconUserRef()} width={24} height={24} />
            </View>
            <View style={styles.itemContent}>
              <Text style={styles.itemContentTitle}>
                {t('FRIENDS_SIGNED_UP_DES')}
              </Text>
            </View>
          </View>
          <View style={styles.itemContainer}>
            <View style={styles.iconContainer}>
              <SvgXml xml={IconGift()} width={24} height={24} />
            </View>
            <View style={styles.itemContent}>
              <Text style={styles.itemContentTitle}>
                {t('YOU_AND_FRIEND_GET_REWARDS_DES')}
              </Text>
            </View>
          </View>
          <Button
            title={t('SHARE_REFERRAL_CODE')}
            variant="primary"
            size="large"
            style={styles.button}
            onPress={handleShare}
          />
        </View>
        <View style={styles.referralCodeContainer}>
          <Text style={styles.referralCodeLabel}>
            {t('YOUR_REFERRAL_CODE')}
          </Text>
          <View style={styles.codeBox}>
            <View style={styles.codeTextContainer}>
              {loading ? (
                <ActivityIndicator size="small" color={colors.yellowLight} />
              ) : (
                <Text style={styles.codeText}>{referralCode}</Text>
              )}
            </View>
            <Button onPress={handleCopy} style={styles.buttonCopy}>
              <SvgXml xml={IconCopy()} width={24} height={24} />
            </Button>
          </View>
        </View>

        {/* Friends Section */}
        <View style={styles.friendsSectionContainer}>
          <TouchableOpacity
            style={styles.friendsListContainer}
            onPress={() => setIsFriendsCollapsed(!isFriendsCollapsed)}
            activeOpacity={0.7}
          >
            <Text style={styles.friendsListTitle}>
              {t('YOUR_REFERRED_FRIENDS')}
            </Text>
            <View style={styles.chevronContainer}>
              <Text style={styles.chevronText}>
                {isFriendsCollapsed ? '▼' : '▲'}
              </Text>
            </View>
          </TouchableOpacity>

          {!isFriendsCollapsed && (
            <>
              <View style={styles.friendsDivider} />
              {referredFriends && referredFriends.length > 0 ? (
                referredFriends.map((friend, index) => (
                  <FriendCard key={friend.id} friend={friend} index={index} />
                ))
              ) : (
                <EmptyComponent
                  text={t('YOU_HAVENT_REFERRED_ANY_FRIENDS_YET')}
                />
              )}
            </>
          )}
        </View>
      </ScrollView>
    </BaseContainer>
  );
}
