import React from 'react';
import { FlatList, View } from 'react-native';
import { BaseContainer, HeaderBase, Text } from 'tradewize-component';
import { styles } from './styles';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { navigationRef } from '../../services/NavigationService';
import { BENEFITS_DATA } from './data';
import { SectionData } from '../../types/insuranceBenefits';

export default function InsuranceBenefitsScreen() {
  const { t } = useAppTranslation();

  const renderSection = ({ item }: { item: SectionData }) => {
    return (
      <View style={styles.sectionCard}>
        <Text style={styles.sectionTitle}>{t(item.sectionTitle)}</Text>
        <View style={styles.benefitsContainer}>
          {item?.benefits?.map(benefit => (
            <View key={benefit?.id} style={[styles.benefitItem]}>
              <Text style={styles.benefitTitle}>{t(benefit?.title)}</Text>
              <Text style={styles.benefitDescription}>
                {t(benefit?.description)}
              </Text>
            </View>
          ))}
        </View>
      </View>
    );
  };

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        title={t('INSURANCE_BENEFITS')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />
      <FlatList
        data={BENEFITS_DATA}
        renderItem={renderSection}
        keyExtractor={(item, index) => `section-${index}`}
        contentContainerStyle={styles.listContent}
        showsVerticalScrollIndicator={false}
      />
    </BaseContainer>
  );
}
