import React from 'react';
import { StyleSheet, View } from 'react-native';
import { useTranslation } from 'react-i18next';
import colors from '../../../theme/colors';
import { formatCurrency } from '../../../utils/curencyUtil';
import { formatDate } from '../../../utils/index';
import { Text } from 'tradewize-component';

interface PaymentIncomeCardProps {
  id: string;
  role: string;
  amount: number;
  paymentDate: string | null;
  estPayDate: string | null;
  paymentStatus: number;
  poNumber: string;
}

const PaymentIncomeCard: React.FC<PaymentIncomeCardProps> = ({
  id,
  role,
  amount,
  paymentStatus,
  paymentDate,
  estPayDate,
  poNumber,
}) => {
  const { t } = useTranslation();
  const expanded = true;

  const getStatusText = () => {
    switch (paymentStatus) {
      case 0:
        return 'Open';
      case 1:
        return 'InComing';
      case 2:
        return 'Paid';
      default:
        return 'Open';
    }
  };

  const getCardBorderColor = () => {
    switch (paymentStatus) {
      case 0: // Open
        return colors.grayScale20;
      case 1: // InComing
        return colors.lightBlue;
      case 2: // Paid
        return colors.greenBackground;
      default:
        return colors.grayScale20;
    }
  };

  const getStatusBackgroundColor = () => '#E3F2FD';

  const getStatusTextColor = () => {
    switch (paymentStatus) {
      case 0: // Open
        return colors.grayScale70;
      case 1: // InComing
        return colors.black;
      case 2: // Paid
        return colors.mainColorPrimary;
      default:
        return colors.grayScale20;
    }
  };

  const formatPaymentLabel = () => {
    if (paymentStatus === 2) {
      return 'Paid Date:';
    }
    return 'Est. Pay Date:';
  };

  const formatDateValue = (dateString: string | null) => {
    if (!dateString) return t('FUTURE');
    const date = new Date(dateString);
    if (isNaN(date.getTime())) return t('FUTURE');
    return formatDate(date) || t('FUTURE');
  };

  const formatAmount = () => {
    const formatted = formatCurrency(amount);
    if (formatted) return formatted;

    const numericValue = Number(amount);
    if (!Number.isFinite(numericValue)) return '';
    return `$${numericValue.toFixed(2)}`;
  };
  return (
    <View
      style={[
        styles.card,
        { borderColor: getCardBorderColor() },
        expanded && styles.cardExpanded,
      ]}
    >
      <View style={styles.cardHeader}>
        <View style={styles.leftSection}>
          <Text style={styles.jobId}>{id}</Text>
          <View style={styles.roleTag}>
            <Text style={styles.roleText}>{role}</Text>
          </View>
        </View>
        <View style={styles.rightSection}>
          <Text style={styles.amount}>{formatAmount()}</Text>
          <View
            style={[
              styles.statusTag,
              { backgroundColor: getStatusBackgroundColor() },
            ]}
          >
            <Text style={[styles.statusText, { color: getStatusTextColor() }]}>
              {getStatusText()}
            </Text>
          </View>
        </View>
      </View>

      <View style={styles.expandedContent}>
        <View style={styles.detailRow}>
          <Text style={styles.label}>{t('PO #')}</Text>
          <Text style={styles.value}>{poNumber}</Text>
        </View>
        <View style={styles.detailRow}>
          <Text style={styles.label}>{formatPaymentLabel()}</Text>
          <Text style={styles.value}>
            {paymentStatus === 2
              ? formatDateValue(paymentDate)
              : formatDateValue(estPayDate)}
          </Text>
        </View>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  card: {
    backgroundColor: colors.white,
    borderRadius: 12,
    padding: 16,
    marginBottom: 12,
    borderWidth: 1,
    borderColor: colors.lightGray2,
    shadowColor: colors.black,
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.1,
    shadowRadius: 4,
    elevation: 3,
  },
  cardExpanded: {
    borderWidth: 1,
    borderColor: colors.lightGray2,
    shadowColor: colors.black,
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.1,
    shadowRadius: 4,
    elevation: 3,
  },
  cardHeader: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
  },
  leftSection: {
    flex: 1,
  },
  rightSection: {
    alignItems: 'flex-end',
  },
  jobId: {
    fontSize: 16,
    color: colors.black,
    marginBottom: 8,
  },
  roleTag: {
    backgroundColor: colors.lightBlueBackground2,
    borderRadius: 12,
    paddingHorizontal: 10,
    paddingVertical: 4,
    alignSelf: 'flex-start',
  },
  roleText: {
    color: colors.lightBlueText,
    fontSize: 12,
  },
  amount: {
    fontSize: 16,
    color: colors.textAmount,
    marginBottom: 4,
  },
  statusTag: {
    borderRadius: 12,
    paddingHorizontal: 10,
    paddingVertical: 4,
    alignSelf: 'flex-end',
    marginTop: 4,
    backgroundColor: colors.lightBlueBackground2,
  },
  statusText: {
    fontSize: 12,
    color: colors.lightBlueText,
  },
  expandedContent: {
    marginTop: 16,
    paddingTop: 16,
    borderTopWidth: 1,
    borderTopColor: colors.basicWhiteGray,
  },
  detailRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginBottom: 12,
  },
  label: {
    fontSize: 14,
    color: colors.twColors.text.secondary,
  },
  value: {
    fontSize: 14,
    color: colors.twColors.text.black,
    fontFamily: 'Medium',
  },
});

export default PaymentIncomeCard;
