import { useCallback, useEffect } from 'react';
import { useDispatch } from 'react-redux';

export const useNetwork = () => {
  const dispatch = useDispatch();

  const fetchGetNetwork = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getNetworkAsync({ keyword: '', page: 2 }));
    } catch (error) {
      console.log(error);
    }
  }, [dispatch]);

  useEffect(() => {
    fetchGetNetwork();
  }, [fetchGetNetwork]);
};
