import { useCallback, useEffect } from 'react';
import { StatusBar } from 'react-native';
import { colors } from '../theme';
import { useFocusEffect } from '@react-navigation/native';
import { useDispatch } from 'react-redux';
import { logger } from '../utils';

interface Props {
  tabActive: 'CONTRACTS_TO_SIGN' | 'SIGNED_CONTRACTS';
}

export const useContract = (props: Props) => {
  const { tabActive } = props;
  const dispatch = useDispatch();

  const fetchGetTemplateContractDetail = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getPendingContractAsync());
    } catch (error) {
      logger('error fetchGetTemplateContractDetail', error);
    }
  }, [dispatch]);

  const fetchGetSignedContract = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getSignedContractAsync());
    } catch (error) {
      logger('error fetchGetSignedContract', error);
    }
  }, [dispatch]);

  useEffect(() => {
    if (tabActive === 'CONTRACTS_TO_SIGN') {
      fetchGetTemplateContractDetail();
    } else {
      fetchGetSignedContract();
    }
  }, [fetchGetTemplateContractDetail, fetchGetSignedContract, tabActive]);

  useFocusEffect(
    useCallback(() => {
      setTimeout(() => {
        StatusBar.setBackgroundColor(colors.mainColorPrimary);
        StatusBar.setBarStyle('light-content');
      }, 300);
    }, []),
  );
};
