import React from 'react';
import { View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { styles } from './styles';
import _ from 'lodash';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';

interface Props {
  activeTab: 'CONTRACTS_TO_SIGN' | 'SIGNED_CONTRACTS';
  handlePressTab: (tab: 'CONTRACTS_TO_SIGN' | 'SIGNED_CONTRACTS') => void;
}

const Tabs = (props: Props) => {
  const { activeTab, handlePressTab } = props;
  const { t } = useAppTranslation();

  return (
    <View style={styles.container}>
      {_.map(['CONTRACTS_TO_SIGN', 'SIGNED_CONTRACTS'], item => (
        <Button
          onPress={() =>
            handlePressTab(item as 'CONTRACTS_TO_SIGN' | 'SIGNED_CONTRACTS')
          }
          variant="ghost"
          key={item}
          style={[styles.tabItem, activeTab === item && styles.tabItemActive]}
        >
          <Text
            style={[
              styles.tabItemText,
              activeTab === item && styles.tabItemTextActive,
            ]}
          >
            {t(item)}
          </Text>
        </Button>
      ))}
    </View>
  );
};

export default Tabs;
