import i18n from '.';
import { STORAGE_KEY, StorageService } from '../services/StorageService';
import { logger } from '../utils';

export async function changeLanguage(lang: string) {
  try {
    const redux = await import('auth/redux');
    const store = redux.store;
    i18n?.changeLanguage(lang);
    await StorageService.set(STORAGE_KEY.LANGUAGE, lang);
    store.dispatch(redux.actionContainer.setLanguage(lang));
  } catch (error) {
    logger('error changeLanguage', error);
  }
}
