import { useEffect, useRef } from 'react';
import * as React from 'react';
import { navigationRef } from './src/services/NavigationService';
import { NavigationContainer } from '@react-navigation/native';
import { KeyboardProvider } from 'react-native-keyboard-controller';
import { Provider } from 'react-redux';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import { GestureHandlerRootView } from 'react-native-gesture-handler';
import ErrorBoundary from './src/components/ErrorBoundary';
import { useApp } from './src/hooks/useApp';
import MainNavigator from './src/navigation/MainNavigator';

const AuthProvider = React.lazy(() => import('auth/AuthProvider'));

const StoreWrapper = ({
  store,
  onReady,
  children,
}: {
  store: any;
  onReady: (store: any) => void;
  children: (store: any) => React.ReactNode;
}) => {
  const hasNotifiedRef = useRef(false);

  useEffect(() => {
    if (store && !hasNotifiedRef.current) {
      hasNotifiedRef.current = true;
      onReady(store);
    }
  }, [store, onReady]);

  return children(store);
};

const Entry = ({
  children,
  onReady,
}: {
  children: (store: any) => React.ReactNode;
  onReady: (store: any) => void;
}) => {
  return (
    <React.Suspense fallback={null}>
      <AuthProvider>
        {(store: any) => {
          return (
            <StoreWrapper store={store} onReady={onReady}>
              {children}
            </StoreWrapper>
          );
        }}
      </AuthProvider>
    </React.Suspense>
  );
};

const MainApp = ({ store }: { store: any }) => {
  if (!store) return null;

  return (
    <NavigationContainer ref={navigationRef}>
      <KeyboardProvider>
        <Provider store={store}>
          <SafeAreaProvider>
            <GestureHandlerRootView>
              <MainNavigator />
            </GestureHandlerRootView>
          </SafeAreaProvider>
        </Provider>
      </KeyboardProvider>
    </NavigationContainer>
  );
};

const App = () => {
  const { loaded, setIsStoreReady } = useApp();

  return (
    <ErrorBoundary name="Root">
      <Entry onReady={s => setIsStoreReady(s)}>
        {store => {
          if (!loaded) {
            return null;
          }
          return <MainApp store={store} />;
        }}
      </Entry>
    </ErrorBoundary>
  );
};

export default App;
