import {
  ActivityIndicator,
  ImageRequireSource,
  StyleProp,
  View,
  ViewStyle,
} from 'react-native';
import FastImage, { ImageStyle, Source } from 'react-native-fast-image';
import { styles } from './styles';
import React, { useState } from 'react';
import { SvgXml } from 'react-native-svg';
import { IconPen } from '../../assets/svg';
import { Button, SCREEN_WIDTH } from 'tradewize-component';
import { colors } from '../../theme';

interface AvatarProps {
  source: Source | ImageRequireSource;
  isEdit?: boolean;
  size?: number;
  style?: StyleProp<ViewStyle>;
  imageStyle?: StyleProp<ImageStyle>;
  styleEdit?: StyleProp<ViewStyle>;
  colorIconEdit?: string;
  iconEdit?: string;
  isLoading?: boolean;

  onPressEdit?: () => void;
  onPress?: () => void;
}

const Avatar = (props: AvatarProps) => {
  const {
    source,
    isEdit = false,
    size = SCREEN_WIDTH * 0.2,
    style,
    imageStyle,
    styleEdit,
    colorIconEdit = colors.black,
    iconEdit,
    isLoading = false,
    onPressEdit,
    onPress,
  } = props;
  const [loading, setLoading] = useState(true);

  return (
    <Button
      variant="ghost"
      disabled={onPress ? false : true}
      onPress={onPress}
      style={[
        styles.container,
        { width: size, height: size, borderRadius: size / 2 },
        !loading && !isLoading && styles.border,
        style,
      ]}
    >
      {(loading || isLoading) && (
        <View style={styles.loading}>
          <ActivityIndicator
            size={size * 0.3}
            color={colors.mainColorPrimary}
          />
        </View>
      )}
      <FastImage
        source={source}
        style={[
          { width: size, height: size, borderRadius: size / 2 },
          imageStyle,
        ]}
        onLoadStart={() => setLoading(true)}
        onLoadEnd={() => setLoading(false)}
      />
      {isEdit && !loading && (
        <Button
          disabled={onPressEdit ? false : true}
          onPress={onPressEdit}
          variant="ghost"
          style={[
            styles.editContainer,
            {
              width: size * 0.35,
              height: size * 0.35,
              borderRadius: (size * 0.35) / 2,
            },
            styleEdit,
          ]}
        >
          <SvgXml
            xml={iconEdit || IconPen(colorIconEdit)}
            width={size * 0.2}
            height={size * 0.2}
          />
        </Button>
      )}
    </Button>
  );
};

export default Avatar;
