export const formatCurrency = (value: number) => {

  
	try {
	  if (!value || Number(value) <= 0) {
		return '';
	  }
	  return new Intl.NumberFormat('en-US', {
		style: 'currency',
		currency: 'USD',
		minimumFractionDigits: 2,
		maximumFractionDigits: 2,
	  }).format(value);
	} catch (error) {
	  return '';
	}
  };