import React from 'react';
import { View, Text, Image } from 'react-native';
import { SvgXml } from 'react-native-svg';
import { myjob } from '../../../assets/images';
import { IconPayment } from '../../../assets/svg';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { ReferralFriend } from '../../../types/referFriend';
import { styles } from '../styles';
import { colors } from '../../../theme';

interface FriendCardProps {
  friend: ReferralFriend;
  index: number;
}

const renderStatus = (status: string) => {
  let statusColor;
  switch (status) {
    case 'Active':
      statusColor = colors.green;
      break;
    case 'Pending':
      statusColor = colors.yellow;
      break;
    case 'Inactive':
      statusColor = colors.red;
      break;
    default:
      statusColor = colors.gray;
  }
  return (
    <Text style={[styles.statusText, { color: statusColor }]}>{status}</Text>
  );
};

export const FriendCard: React.FC<FriendCardProps> = ({ friend, index }) => {
  const { t } = useAppTranslation();

  return (
    <View
      key={friend.id}
      style={[styles.friendCardWrapper, index === 0 && styles.firstCardWrapper]}
    >
      <View style={styles.friendCard}>
        <View style={styles.cardHeader}>
          <View style={styles.avatarPlaceholder}>
            <Text style={styles.avatarText}>
              {friend?.fullName?.charAt(0)?.toUpperCase() || 'U'}
            </Text>
          </View>
          <View style={styles.headerInfo}>
            <Text style={styles.name}>{friend?.fullName}</Text>
            {renderStatus(friend?.statusName || '')}
          </View>
        </View>
        <View style={styles.statsContainer}>
          <View style={styles.statItem}>
            <Image source={myjob} style={styles.statIcon} />
            <View style={styles.statContent}>
              <Text style={styles.statLabel}>{t('JOBS_COMPLETED')}</Text>
              <Text style={styles.statValueJob}>{friend?.totalJobs || 0}</Text>
            </View>
          </View>
          <View style={styles.statDivider} />
          <View style={styles.statItem}>
            <SvgXml xml={IconPayment()} width={24} height={24} />
            <View style={styles.statContent}>
              <Text style={styles.statLabel}>{t('TOTAL_EARNINGS')}</Text>
              <Text style={styles.statValueEar}>
                {friend?.earning ? `$${friend.earning}` : '$0'}
              </Text>
            </View>
          </View>
        </View>
      </View>
    </View>
  );
};
