import React, { useCallback } from 'react';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { SvgXml } from 'react-native-svg';

import HomeScreen from '../screens/Home';
import colors from '../theme/colors';
import {
  IconHome,
  IconMyJobs,
  IconChatMessenger,
  IconAccount,
} from '../assets/svg';
import MyJobScreen from '../screens/MyJobScreen';
import AccountScreen from '../screens/Account';
import { SCREEN_ENUM } from '../types/global';
import { useAppTranslation } from '../hooks/useAppTranslation';

const Tab = createBottomTabNavigator();

// Hàm trả về icon SVG tương ứng với index
const getTabBarIcon = (index: number, focused: boolean) => {
  const color = focused ? colors.mainColorPrimary : colors.black;

  let iconXml = '';
  switch (index) {
    case 0:
      iconXml = IconHome(color);
      break;
    case 1:
      iconXml = IconMyJobs(color);
      break;
    case 2:
      iconXml = IconChatMessenger(color);
      break;
    case 3:
      iconXml = IconAccount(color);
      break;
    default:
      iconXml = IconHome(color);
  }

  return <SvgXml xml={iconXml} />;
};

const MainBottomTabNavigator = () => {
  const { t } = useAppTranslation();

  const screenOptions = useCallback(({ route }: { route: any }) => {
    const isTabScreen = [SCREEN_ENUM.MyJobScreen].includes(route.name);
    return {
      headerShown: false,
      lazy: false,
      tabBarStyle: {
        display: isTabScreen ? 'none' : ('flex' as 'none' | 'flex'),
      },
    };
  }, []);

  return (
    <Tab.Navigator
      initialRouteName={SCREEN_ENUM.HomeScreen}
      screenOptions={screenOptions}
    >
      <Tab.Screen
        name={SCREEN_ENUM.HomeScreen}
        options={{
          tabBarIcon: ({ focused }: { focused: boolean }) =>
            getTabBarIcon(0, focused),
          tabBarLabel: t('HOME'),
        }}
        component={HomeScreen}
      />
      <Tab.Screen
        name={SCREEN_ENUM.MyJobScreen}
        options={{
          tabBarIcon: ({ focused }: { focused: boolean }) =>
            getTabBarIcon(1, focused),
          tabBarLabel: t('JOBS'),
        }}
        component={MyJobScreen}
      />
      <Tab.Screen
        name={SCREEN_ENUM.ChatScreen}
        options={{
          tabBarIcon: ({ focused }: { focused: boolean }) =>
            getTabBarIcon(2, focused),
          tabBarLabel: t('MESSAGE'),
          tabBarBadge: '9',
          tabBarBadgeStyle: {
            color: colors.white,
            backgroundColor: colors.red,
            minWidth: 0,
          },
        }}
        component={HomeScreen}
      />
      <Tab.Screen
        name={SCREEN_ENUM.AccountScreen}
        options={{
          tabBarIcon: ({ focused }: { focused: boolean }) =>
            getTabBarIcon(3, focused),
          tabBarLabel: t('ACCOUNT'),
        }}
        component={AccountScreen}
      />
    </Tab.Navigator>
  );
};

export default MainBottomTabNavigator;
