import React from 'react';
import { View, ViewStyle, StyleProp, StyleSheet } from 'react-native';
import { getStrengthColor } from './helper';
import colors from '../../../theme/colors';

interface PasswordStrengthBarProps {
  score: number;
  style?: StyleProp<ViewStyle>;
}

const PasswordStrengthBar = ({ score, style }: PasswordStrengthBarProps) => {
  const styles = createStyles();
  return (
    <View style={style}>
      <View style={styles.bar}>
        {[...Array(4)].map((_, i) => (
          <View
            key={i}
            style={[
              styles.status,
              {
                backgroundColor:
                  i < score
                    ? getStrengthColor(score)
                    : colors.additionalSecondary,
              },
            ]}
          />
        ))}
      </View>
    </View>
  );
};

const createStyles = () =>
  StyleSheet.create({
    bar: {
      flexDirection: 'row',
      justifyContent: 'space-between',
      width: '100%',
      height: 10,
      gap: 2,
    },
    status: {
      flex: 1,
      height: '100%',
      borderRadius: 8,
    },
  });

export default PasswordStrengthBar;
