import { logger } from '../utils';
import { createLocation } from '../api/endpoint';

export interface LocationData {
  latitude: number;
  longitude: number;
  accuracy?: number;
  speed?: number;
  heading?: number;
  altitude?: number;
  timestamp: string;
}

export class LocationService {
  private static instance: LocationService;

  private constructor() {}

  public static getInstance(): LocationService {
    if (!LocationService.instance) {
      LocationService.instance = new LocationService();
    }
    return LocationService.instance;
  }

  async sendLocationUpdate(location: LocationData): Promise<void> {
    try {
      const client = await import('auth/api');
      const redux = await import('auth/redux');
      const userId = redux.store.getState()?.auth?.userProfile?.accountId;
      const token = redux.store.getState().auth.token;

      const payload = {
        userId: userId,
        location: {
          latitude: location.latitude,
          longitude: location.longitude,
        },
        locationTimestamp: new Date(location.timestamp).toISOString(),
      };
      if (token) {
        await client.postRequest(createLocation, payload);
      }
      logger('[LocationService] Location update sent successfully');
    } catch (error) {
      logger('[LocationService] Error sending location update:', error);
      throw error;
    }
  }
}

export default LocationService;
