import React, { useMemo, useState } from 'react';
import { ActivityIndicator, FlatList, Platform, View } from 'react-native';
import { BaseContainer, Button, HeaderBase, Text } from 'tradewize-component';
import { styles } from './styles';
import { colors } from '../../theme';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { NavigationService } from '../../services/NavigationService';
import { useContract } from '../../hooks/useContract';
import { useSelector } from 'react-redux';
import { AutoSkeletonView } from 'react-native-auto-skeleton';
import EmptyComponent from './components/EmptyComponent';
import WebView from 'react-native-webview';
import dayjs from 'dayjs';
import CanvasView from './components/Canvas';
import Tabs from './components/Tabs';
import _ from 'lodash';
import { SignedContract } from '../../types/contract';
import PdfView from './components/Pdf';

const ContractScreen = () => {
  const { t } = useAppTranslation();
  const [isContractAccepted, setIsContractAccepted] = useState<boolean>(false);
  const [activeTab, setActiveTab] = useState<
    'CONTRACTS_TO_SIGN' | 'SIGNED_CONTRACTS'
  >('CONTRACTS_TO_SIGN');

  const pendingContract = useSelector(
    (state: any) => state?.contract?.pendingContract,
  );
  const templateContractDetail = useSelector(
    (state: any) => state?.contract?.templateContractDetail,
  );
  const signedContract = useSelector(
    (state: any) => state?.contract?.signedContract,
  );

  const [selectedContract, setSelectedContract] =
    useState<SignedContract | null>(null);
  const [isShowPdf, setIsShowPdf] = useState<boolean>(false);

  const styledHtml = `
    <html>
      <head>
        <style>
          ol {
            list-style-type: disc !important;
            padding-left: 20px;
          }
        </style>
      </head>
      <body>
        ${templateContractDetail?.data?.contractHtml || ''}
      </body>
    </html>
  `;

  useContract({ tabActive: activeTab });

  const renderContent = useMemo(() => {
    if (pendingContract?.isLoading) {
      return (
        <AutoSkeletonView isLoading={pendingContract?.isLoading}>
          <View style={styles.cardCompanySkeleton} />
        </AutoSkeletonView>
      );
    }

    if (pendingContract?.data?.length > 0) {
      return (
        <View style={styles.cardCompany}>
          <Text style={styles.textCompany} numberOfLines={1}>
            {t('COMPANY')}: {pendingContract?.data?.[0]?.companyName}
          </Text>
          <Text style={styles.currentCompany} numberOfLines={1}>
            1/{pendingContract?.data?.length}
          </Text>
        </View>
      );
    }

    return null;
  }, [pendingContract?.data, pendingContract?.isLoading, t]);

  const renderContentSignedContracts = useMemo(() => {
    if (signedContract?.isLoading) {
      return (
        <AutoSkeletonView isLoading={signedContract?.isLoading}>
          {_.times(5, index => (
            <View style={styles.cardContractSkeleton} key={index} />
          ))}
        </AutoSkeletonView>
      );
    }

    return (
      <FlatList
        data={signedContract?.data}
        keyExtractor={item => item?.id?.toString()}
        renderItem={({ item }) => (
          <Button
            variant="ghost"
            style={styles.cardContract}
            onPress={() => {
              setSelectedContract(item);
              setIsShowPdf(true);
            }}
          >
            <Text style={styles.nameContract} numberOfLines={1}>
              {item?.companyName} {t('CONTRACT')}
            </Text>
            <Text style={styles.dateContract} numberOfLines={1}>
              {t('SIGNED_ON')}: {dayjs(item?.createdAt).format('MM/DD/YYYY')}
            </Text>
          </Button>
        )}
      />
    );
  }, [signedContract?.data, signedContract?.isLoading, t]);

  const renderWebView = useMemo(() => {
    if (templateContractDetail?.isLoading || pendingContract?.isLoading) {
      return (
        <View style={styles.webViewContainerSkeleton}>
          <ActivityIndicator size="large" color={colors.mainColorPrimary} />
        </View>
      );
    }

    if (!templateContractDetail?.data?.contractHtml) {
      return null;
    }

    return (
      <View style={styles.webViewContainer}>
        <WebView
          originWhitelist={['*']}
          source={{ html: styledHtml }}
          style={styles.webView}
          scalesPageToFit={true}
          zoomEnabled={true}
          javaScriptEnabled={true}
          domStorageEnabled={true}
          injectedJavaScript={`
                    document.body.style.fontSize = '2em';
                    ${
                      Platform.OS === 'ios'
                        ? 'document.getElementsByTagName("html")[0].style.zoom = 2;'
                        : 'document.body.style.transform = "scale(2)"; document.body.style.transformOrigin = "0 0";'
                    }
                    window.ReactNativeWebView.postMessage('Zoom applied');
                    true;
                  `}
        />
        <View style={styles.footerContainer}>
          <View style={styles.footerTimer}>
            <Text style={styles.footerTimerText}>
              {dayjs().format('MM/DD/YYYY')}
            </Text>
          </View>
          <Button
            onPress={() => setIsContractAccepted(true)}
            size="large"
            variant="primary"
            style={styles.footerButton}
            title={t('ACCEPT_CONTRACT')}
          />
        </View>
      </View>
    );
  }, [
    styledHtml,
    templateContractDetail?.isLoading,
    templateContractDetail?.data?.contractHtml,
    t,
    pendingContract?.isLoading,
  ]);

  const renderContractsToSign = useMemo(() => {
    return (
      <View style={styles.signedContractsContainer}>
        {renderContent}

        {renderWebView}

        {pendingContract?.data?.length === 0 &&
          !pendingContract?.isLoading &&
          !templateContractDetail?.isLoading &&
          !templateContractDetail?.data?.contractHtml &&
          !templateContractDetail?.error && (
            <EmptyComponent text={t('NO_CONTRACTS_NEED_TO_BE_SIGNED')} />
          )}
      </View>
    );
  }, [
    pendingContract?.data?.length,
    pendingContract?.isLoading,
    renderContent,
    renderWebView,
    templateContractDetail?.data?.contractHtml,
    templateContractDetail?.error,
    templateContractDetail?.isLoading,
    t,
  ]);

  const renderSignedContracts = useMemo(() => {
    return (
      <View style={styles.contractsToSignContainer}>
        {renderContentSignedContracts}

        {signedContract?.data?.length === 0 && !signedContract?.isLoading && (
          <EmptyComponent text={t('NO_SIGNED_CONTRACTS_YET')} />
        )}
      </View>
    );
  }, [
    renderContentSignedContracts,
    signedContract?.data?.length,
    signedContract?.isLoading,
    t,
  ]);

  return (
    <BaseContainer
      backgroundColor={colors.mainColorPrimary}
      style={styles.container}
      backgroundStatusBarColor={colors.mainColorPrimary}
      statusBarStyle="light-content"
      backgroundBottomSafeAreaColor={colors.white}
    >
      <HeaderBase
        title={t('CONTRACTS')}
        titleStyle={{ color: colors.white }}
        backgroundColor={colors.mainColorPrimary}
        colorIconLeft={colors.white}
        onPressBack={() => {
          NavigationService.goBack();
        }}
      />
      <Tabs activeTab={activeTab} handlePressTab={setActiveTab} />

      {activeTab === 'CONTRACTS_TO_SIGN'
        ? renderContractsToSign
        : renderSignedContracts}

      <CanvasView
        onClose={() => setIsContractAccepted(false)}
        visible={isContractAccepted}
      />

      <PdfView
        onClose={() => {
          setIsShowPdf(false);
          setSelectedContract(null);
        }}
        visible={isShowPdf}
        contract={selectedContract}
      />
    </BaseContainer>
  );
};

export default ContractScreen;
