import React from 'react';
import { View, ScrollView } from 'react-native';
import { BaseContainer, Button } from 'tradewize-component';
import { styles } from './styles';

import { Section } from './type';
import { DATA } from './model';
import SestionCard from './Components/SestionCard';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import Header from './Components/Header';
import { useDispatch } from 'react-redux';
import { logger } from '../../utils';
import { colors } from '../../theme';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { useAccount } from '../../hooks/useAccount';

const AccountScreen = () => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();
  const insets = useSafeAreaInsets();

  useAccount();

  const handleLogout = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showConfirmModal({
          isShowCloseButton: false,
          isBackdropPress: false,
          isShowIcon: true,
          title: t('LOGOUT'),
          description: t('ARE_YOU_SURE_YOU_WANT_TO_LOGOUT'),
          confirmText: t('CONFIRM'),
          cancelText: t('CANCEL'),
          onConfirm: () => {
            dispatch(redux.actionContainer.hideConfirmModal());
            setTimeout(() => {
              dispatch(redux.actionContainer.logout());
            }, 350);
          },
          onCancel: () => {
            dispatch(redux.actionContainer.hideConfirmModal());
          },
        }),
      );
    } catch (error) {
      logger('error handleLogout', error);
    }
  };

  return (
    <BaseContainer
      backgroundColor={colors.mainColorPrimary}
      statusBarStyle="light-content"
      style={[styles.container, { paddingTop: insets.top }]}
      isPaddingToSafeArea={false}
    >
      <View style={styles.safeArea}>
        <ScrollView
          bounces={false}
          alwaysBounceVertical={false}
          style={styles.scrollView}
          showsVerticalScrollIndicator={false}
          contentContainerStyle={styles.contentContainer}
        >
          <Header />
          {DATA.map((section: Section, index: number) => (
            <SestionCard key={index} section={section} sectionIndex={index} />
          ))}
          <View style={styles.logoutContainer}>
            <Button
              variant="secondary"
              style={styles.logoutButton}
              onPress={handleLogout}
              activeOpacity={0.7}
              title={t('LOGOUT')}
              textStyle={styles.logoutButtonText}
            />
          </View>
        </ScrollView>
      </View>
    </BaseContainer>
  );
};

export default AccountScreen;
