import React, { useEffect, useMemo, useState } from 'react';
import { ActivityIndicator, ScrollView, View } from 'react-native';
import {
  BaseContainer,
  Button,
  HeaderBase,
  SCREEN_WIDTH,
  Text,
} from 'tradewize-component';
import { styles } from './styles';
import FastImage from 'react-native-fast-image';
import { navigationRef } from '../../services/NavigationService';
import { colors } from '../../theme';
import { useDispatch, useSelector } from 'react-redux';
import { new_user } from '../../assets/images';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import Avatar from '../../components/Avatar';
import { logger } from '../../utils';
import ModalChangeAvatar from './components/ModalChangeAvatar';
import InfoRow from './components/InfoRow';
import { ShadowedView } from 'react-native-fast-shadow';
import { shadowGlobal } from '../../types/global';
import _ from 'lodash';

export default function UserProfileScreen() {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();
  const userProfile = useSelector((state: any) => state.auth.userProfile);
  const userAvatar = useSelector((state: any) => state.user.userAvatar);

  const [isLoadingIdImage, setIsLoadingIdImage] = useState(false);
  const [isEditProfile, setIsEditProfile] = useState(false);
  const [inputValues, setInputValues] = useState<Record<string, string>>({});

  const onPressChangeAvatar = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showCustomModal({
          children: <ModalChangeAvatar />,
          isBackdropPress: false,
          animationIn: 'zoomIn',
          animationOut: 'zoomOut',
          animationInTiming: 400,
          animationOutTiming: 400,
          backdropOpacity: 0,
        }),
      );
    } catch (error) {
      logger('error onPressChangeAvatar', error);
    }
  };

  const handleChange = ({ key, value }: { key: string; value: string }) => {
    try {
      setInputValues(prev => ({
        ...prev,
        [key]: value,
      }));
    } catch (error) {
      logger('error handleChange', error);
    }
  };

  const handlePressEdit = async () => {
    try {
      const redux = await import('auth/redux');
      const requiredFields = [
        Object.keys(inputValues)[0],
        Object.keys(inputValues)[1],
      ];
      const emptyFields = requiredFields.filter(
        key => !inputValues[key] || inputValues[key].trim() === '',
      );

      if (emptyFields?.length > 0) {
        dispatch(
          redux.actionContainer.showAlertAsync({
            title: t('WARNING'),
            message: t('CANNOT_BE_EMPTY', {
              field: emptyFields.join(', '),
            }),
            type: 'warning',
            buttonText: t('OK'),
          }),
        );
        return;
      }

      dispatch(
        redux.actionContainer.showConfirmModal({
          isShowCloseButton: false,
          isBackdropPress: false,
          isShowIcon: true,
          title: t('WARNING'),
          description: t('ARE_YOU_SURE_YOU_WANT_TO_EDIT_YOUR_PROFILE'),
          confirmText: t('CONFIRM'),
          cancelText: t('CANCEL'),
          onConfirm: () => {
            setIsEditProfile(false);
            dispatch(redux.actionContainer.hideConfirmModal());
            dispatch(
              redux.actionContainer.setUserProfileAsync({
                FirstName: inputValues[t('FIRST_NAME')],
                LastName: inputValues[t('LAST_NAME')],
              }),
            );
          },
        }),
      );
    } catch (error) {
      logger('error handlePressEdit', error);
    }
  };

  const handlePressCancelEdit = () => {
    try {
      setIsEditProfile(false);
      setInputValues({
        [t('FIRST_NAME')]: userProfile?.firstName || '',
        [t('LAST_NAME')]: userProfile?.lastName || '',
        [t('PHONE_NO')]: userProfile?.phone || '',
        [t('EMAIL')]: userProfile?.email || '',
        [t('STREET_ADDRESS')]: address?.street || userProfile?.street || '',
        [t('CITY')]: address?.city || userProfile?.city || '',
        [t('STATE')]: address?.state || userProfile?.state || '',
        [t('ZIP_CODE')]: address?.zipCode || userProfile?.zipCode || '',
      });
    } catch (error) {
      logger('error handlePressCancelEdit', error);
    }
  };

  const address = useMemo(() => {
    if (userProfile?.addresses?.length) {
      return userProfile.addresses[0];
    }
    return null;
  }, [userProfile?.addresses]);

  const identificationImage = useMemo(() => {
    return (
      userProfile?.identificationNumbers?.[0]?.imgLink ||
      userProfile?.identificationNumbers?.[0]?.identificationNumberPhoto?.url
    );
  }, [userProfile?.identificationNumbers]);

  const avatarSource = useMemo(() => {
    if (userProfile?.photoUrl) return { uri: userProfile.photoUrl };
    return new_user;
  }, [userProfile?.photoUrl]);

  useEffect(() => {
    if (userProfile && address) {
      setInputValues({
        [t('FIRST_NAME')]: userProfile?.firstName || '',
        [t('LAST_NAME')]: userProfile?.lastName || '',
        [t('PHONE_NO')]: userProfile?.phone || '',
        [t('EMAIL')]: userProfile?.email || '',
        [t('STREET_ADDRESS')]: address?.street || userProfile?.street || '',
        [t('CITY')]: address?.city || userProfile?.city || '',
        [t('STATE')]: address?.state || userProfile?.state || '',
        [t('ZIP_CODE')]: address?.zipCode || userProfile?.zipCode || '',
      });
    }
  }, [userProfile, address, t]);

  return (
    <BaseContainer
      backgroundColor={colors.white}
      style={styles.container}
      backgroundStatusBarColor={colors.white}
    >
      <HeaderBase
        title={t('PERSONAL_INFORMATION')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />
      <ScrollView
        style={styles.content}
        contentContainerStyle={styles.contentContainer}
        showsVerticalScrollIndicator={false}
      >
        <View style={styles.profileSection}>
          <Avatar
            source={avatarSource}
            isLoading={userAvatar.isLoading}
            isEdit={true}
            size={SCREEN_WIDTH * 0.22}
            onPressEdit={onPressChangeAvatar}
          />

          <Text style={styles.userName}>
            {userProfile?.fullName ||
              `${userProfile?.firstName || ''} ${
                userProfile?.lastName || ''
              }`.trim() ||
              t('USER_NAME')}
          </Text>
        </View>

        <ShadowedView style={[shadowGlobal, styles.infoContainer]}>
          {_.times(4, index => {
            const key = Object.keys(inputValues)[index];
            const value = inputValues[key];

            return (
              <InfoRow
                key={index}
                data={{ [key]: value }}
                handleChange={handleChange}
                isEdit={index < 2 ? isEditProfile : false}
              />
            );
          })}
        </ShadowedView>

        <ShadowedView style={[shadowGlobal, styles.infoContainer]}>
          {_.times(4, index => {
            const key = Object.keys(inputValues)[index + 4];
            const value = inputValues[key];

            return <InfoRow key={index} data={{ [key]: value }} />;
          })}
        </ShadowedView>

        <ShadowedView style={[shadowGlobal, styles.infoContainer]}>
          <Text style={styles.sectionTitle}>
            {t('OTHER', { defaultValue: 'Other' })}
          </Text>
          <View style={styles.otherImageWrapper}>
            {identificationImage ? (
              <FastImage
                source={{ uri: identificationImage }}
                style={styles.otherImage}
                resizeMode="cover"
                onLoadStart={() => setIsLoadingIdImage(true)}
                onLoadEnd={() => setIsLoadingIdImage(false)}
              />
            ) : (
              <View style={[styles.otherImage, styles.defaultLicenseImage]}>
                <Text style={styles.placeholderText}>-</Text>
              </View>
            )}
            {isLoadingIdImage && (
              <View style={styles.loadingOverlay}>
                <ActivityIndicator color={colors.mainColorPrimary} />
              </View>
            )}
          </View>
        </ShadowedView>
      </ScrollView>

      <View
        style={[
          styles.editButtonContainer,
          isEditProfile && styles.editButtonContainerActive,
        ]}
      >
        <Button
          title={isEditProfile ? t('SAVE') : t('EDIT_PROFILE')}
          style={[styles.editButton, isEditProfile && styles.flexOne]}
          textStyle={styles.editButtonText}
          onPress={() =>
            isEditProfile ? handlePressEdit() : setIsEditProfile(true)
          }
        />

        {isEditProfile && (
          <Button
            variant="secondary"
            title={t('CANCEL')}
            style={[
              styles.editButton,
              styles.editButtonCancel,
              isEditProfile && styles.flexOne,
            ]}
            textStyle={[styles.editButtonText, styles.editButtonCancelText]}
            onPress={handlePressCancelEdit}
          />
        )}
      </View>
    </BaseContainer>
  );
}
