import { useCallback, useEffect, useRef } from 'react';
import React from 'react';
import { logger } from '../utils';
import {
  getCurrentLocation,
  requestPermissionLocationWhenInUse,
} from '../helper';
import BackgroundTimer from 'react-native-background-timer';
import { useAppState } from './useAppState';
import { Platform } from 'react-native';
import { check, PERMISSIONS, RESULTS } from 'react-native-permissions';
import LocationService from '../services/LocationService';
import { changeLanguage } from '../i18n/languageManager';
import { useSelector } from 'react-redux';

const BACKGROUND_TIMER_INTERVAL = 15 * 60 * 1000; // 15 minutes

export const useApp = () => {
  const appState = useAppState();
  const intervalIdRef = useRef<number | null>(null);

  const [loaded, setLoaded] = React.useState(false);
  const [isStoreReady, setIsStoreReady] = React.useState<any>(null);
  const [isRequestLocationPermission, setIsRequestLocationPermission] =
    React.useState(false);

  const language = useSelector((state: any) => state.app.language);

  const stopBackgroundTimer = useCallback(() => {
    if (intervalIdRef.current !== null) {
      BackgroundTimer.clearInterval(intervalIdRef.current);
      intervalIdRef.current = null;
    }
  }, []);

  const sendLocationUpdate = useCallback(async () => {
    try {
      const location = await getCurrentLocation();
      await LocationService.getInstance().sendLocationUpdate({
        latitude: location?.latitude,
        longitude: location?.longitude,
        timestamp: location?.timestamp,
      });
    } catch (error) {
      logger('error sendLocationUpdate', error);
    }
  }, []);

  const startBackgroundTimer = useCallback(() => {
    // Don't start if timer is already running
    if (intervalIdRef.current !== null) {
      return;
    }

    // Send location update immediately on first call
    sendLocationUpdate();

    // Start new interval timer for subsequent updates
    const intervalId = BackgroundTimer.setInterval(() => {
      sendLocationUpdate();
    }, BACKGROUND_TIMER_INTERVAL);

    intervalIdRef.current = intervalId;
  }, [sendLocationUpdate]);

  const initializationProcess = useCallback(async () => {
    try {
      const permissionLocationWhenInUseResult =
        await requestPermissionLocationWhenInUse();

      if (permissionLocationWhenInUseResult.granted) {
        startBackgroundTimer();
      } else {
        stopBackgroundTimer();
      }

      setIsRequestLocationPermission(permissionLocationWhenInUseResult.status);
    } catch (error) {
      logger('error initializationProcess', error);
    }
  }, [startBackgroundTimer, stopBackgroundTimer]);

  const checkPermissionLocationWhenInUse = useCallback(async () => {
    const permission =
      Platform.OS === 'ios'
        ? PERMISSIONS.IOS.LOCATION_WHEN_IN_USE
        : PERMISSIONS.ANDROID.ACCESS_FINE_LOCATION;

    const status = await check(permission);
    if (status === RESULTS.GRANTED) {
      startBackgroundTimer();
    } else {
      stopBackgroundTimer();
    }
  }, [startBackgroundTimer, stopBackgroundTimer]);

  useEffect(() => {
    if (appState === 'active') {
      checkPermissionLocationWhenInUse();
    } else {
      // Stop timer when app goes to background
      stopBackgroundTimer();
    }
  }, [appState, checkPermissionLocationWhenInUse, stopBackgroundTimer]);

  useEffect(() => {
    initializationProcess();
  }, [initializationProcess]);

  useEffect(() => {
    if (isStoreReady && !loaded && isRequestLocationPermission) {
      setLoaded(true);
    }
  }, [isStoreReady, loaded, isRequestLocationPermission]);

  // Cleanup timer on unmount
  useEffect(() => {
    return () => {
      stopBackgroundTimer();
    };
  }, [stopBackgroundTimer]);

  const setLanguage = useCallback(async () => {
    try {
      changeLanguage(language || 'en');
    } catch (error) {
      logger('error setLanguage', error);
      changeLanguage('en');
    }
  }, [language]);

  useEffect(() => {
    setLanguage();
  }, [setLanguage]);

  return {
    loaded,
    isStoreReady,
    setIsStoreReady,
  };
};
