import React, { useCallback, useEffect, useRef, useState } from 'react';
import { ActivityIndicator, Animated, View } from 'react-native';
import { styles } from './styles';
import { Button, SCREEN_HEIGHT, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { IconClose } from '../../../../assets/svg';
import { colors } from '../../../../theme';
import { ShadowedView } from 'react-native-fast-shadow';
import { shadowGlobal } from '../../../../types/global';
import Signature, { SignatureViewRef } from 'react-native-signature-canvas';
import FastImage from 'react-native-fast-image';
import { undo, updateArrow } from '../../../../assets/images';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { base64ToBinaryForFormData, logger } from '../../../../utils';
import Toast from 'react-native-toast-message';
import { useDispatch, useSelector } from 'react-redux';

const HEIGHT_SIGNATURE = SCREEN_HEIGHT * 0.5;

interface Props {
  visible: boolean;
  onClose: () => void;
}

const CanvasView = ({ visible, onClose }: Props) => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();

  const scale = useRef(new Animated.Value(0.8)).current;
  const opacity = useRef(new Animated.Value(0)).current;
  const signatureRef = useRef<SignatureViewRef>(null);

  const pendingContract = useSelector(
    (state: any) => state?.contract?.pendingContract,
  );
  const saveSignatureContract = useSelector(
    (state: any) => state?.contract?.saveSignatureContract,
  );

  const [shouldRender, setShouldRender] = useState(false);
  const [signature, setSignature] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState<boolean>(false);

  const handleOK = (s: string) => {
    setSignature(s);
    setIsSubmitting(true);
  };

  const onClear = useCallback(() => {
    try {
      signatureRef.current?.clearSignature();
      setIsSubmitting(false);
      setSignature(null);
    } catch (error) {
      logger('error onClear', error);
    }
  }, [signatureRef]);

  const onUndo = useCallback(() => {
    signatureRef.current?.undo();
  }, [signatureRef]);

  const handleSave = async () => {
    try {
      const redux = await import('auth/redux');
      if (!isSubmitting) {
        signatureRef.current?.readSignature();
      } else {
        const CompanyId = pendingContract?.data?.[0]?.companyId;
        const UserType = pendingContract?.data?.[0]?.userType?.toString();
        const signatureFile = base64ToBinaryForFormData(
          signature,
          `${CompanyId}.png`,
          'image/png',
        );

        dispatch(
          redux.actionContainer.saveSignatureContractAsync({
            companyId: CompanyId,
            userType: UserType,
            signatureFile: signatureFile,
          }),
        );
        // handleClose();
      }
    } catch (error) {
      logger('error handleSave', error);
    }
  };

  const handleEmpty = () => {
    try {
      Toast.show({
        text1: t('A_SIGNATURE_IS_REQUIRED_BEFORE_SENDING'),
        type: 'error',
      });
      setIsSubmitting(false);
    } catch (error) {
      logger('error handleEmpty', error);
    }
  };

  const handleClose = useCallback(() => {
    onClose?.();
  }, [onClose]);

  const submitSuccess = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      handleClose();
      onClear();
      dispatch(redux.actionContainer.clearSaveSignatureContract());
    } catch (error) {
      logger('error submitSuccess', error);
    }
  }, [handleClose, onClear, dispatch]);

  useEffect(() => {
    if (visible) {
      setShouldRender(true);

      Animated.parallel([
        Animated.spring(scale, {
          toValue: 1,
          useNativeDriver: true,
        }),
        Animated.timing(opacity, {
          toValue: 1,
          duration: 200,
          useNativeDriver: true,
        }),
      ]).start();
    } else {
      Animated.parallel([
        Animated.timing(scale, {
          toValue: 0.8,
          duration: 150,
          useNativeDriver: true,
        }),
        Animated.timing(opacity, {
          toValue: 0,
          duration: 150,
          useNativeDriver: true,
        }),
      ]).start(() => {
        // Chỉ unmount component sau khi animation kết thúc
        setShouldRender(false);
      });
    }
  }, [visible, scale, opacity]);

  useEffect(() => {
    if (saveSignatureContract?.data && !saveSignatureContract?.isLoading) {
      submitSuccess();
    }
  }, [
    saveSignatureContract?.data,
    saveSignatureContract?.isLoading,
    submitSuccess,
  ]);

  if (!shouldRender) return null;

  return (
    <View style={styles.overlay}>
      <Animated.View
        style={[
          styles.content,
          {
            transform: [{ scale }],
            opacity,
          },
        ]}
      >
        <ShadowedView style={[styles.contentContainer, shadowGlobal]}>
          {saveSignatureContract?.isLoading && (
            <View style={styles.loadingContainer}>
              <ActivityIndicator size="large" color={colors.mainColorPrimary} />
            </View>
          )}
          <View style={styles.contentContainerHeader}>
            <Text style={styles.contentContainerHeaderTitle}>
              {t('SIGN_CONTRACT')}
            </Text>
            <Button
              style={styles.contentContainerHeaderCloseButton}
              variant="ghost"
              onPress={handleClose}
            >
              <SvgXml xml={IconClose(colors.white)} width={16} height={16} />
            </Button>
          </View>

          <View style={styles.contentContainerCanvas}>
            {signature && (
              <FastImage
                source={{ uri: signature }}
                style={styles.signatureImage}
              />
            )}
            <Signature
              ref={signatureRef}
              minWidth={4}
              onOK={handleOK}
              onEmpty={handleEmpty}
              penColor={colors.black}
              showsVerticalScrollIndicator={false}
              webviewContainerStyle={styles.webviewContainerStyle}
              webStyle={`
                .m-signature-pad--footer { display: none; }
                .m-signature-pad {
                  flex: 1;
                  border-radius: 12px;
                  height: ${HEIGHT_SIGNATURE}px;
                  background-color: ${colors.basicWhiteGray};
                }
                .m-signature-pad canvas {
                background-image: url('${colors.basicWhiteGray}');
                background-size: 100% 100%;
                background-repeat: no-repeat;
                }
              `}
            />
          </View>

          <View style={styles.contentContainerFooter}>
            <Button
              variant="secondary"
              onPress={onClear}
              style={styles.buttonClear}
            >
              <FastImage
                tintColor={colors.mainColorPrimary}
                source={updateArrow}
                style={styles.buttonClearImage}
              />
            </Button>
            {!isSubmitting && !signature && (
              <Button
                variant="secondary"
                onPress={onUndo}
                style={styles.buttonUndo}
              >
                <FastImage
                  tintColor={colors.mainColorPrimary}
                  source={undo}
                  style={styles.buttonUndoImage}
                />
              </Button>
            )}

            <Button
              variant="primary"
              onPress={handleSave}
              style={styles.buttonSave}
            >
              <Text style={styles.buttonSaveText}>
                {isSubmitting ? t('SUBMIT') : t('SAVE')}
              </Text>
            </Button>
          </View>
        </ShadowedView>
      </Animated.View>
    </View>
  );
};

export default CanvasView;
