import { Animated, Easing, View } from 'react-native';
import { styles } from './styles';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { SignedContract } from '../../../../types/contract';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { IconClose } from '../../../../assets/svg';
import { colors } from '../../../../theme';
import { SvgXml } from 'react-native-svg';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import PdfView from 'react-native-pdf';

interface Props {
  visible: boolean;
  contract: SignedContract | null;
  onClose?: () => void;
}

const Pdf = ({ visible, contract, onClose }: Props) => {
  const { t } = useAppTranslation();
  const translateY = useRef(new Animated.Value(50)).current;
  const opacity = useRef(new Animated.Value(0)).current;

  const [shouldRender, setShouldRender] = useState(false);
  const [error, setError] = useState<boolean>(false);

  const handleClose = useCallback(() => {
    onClose?.();
    setError(false);
  }, [onClose, setError]);

  useEffect(() => {
    if (visible) {
      setShouldRender(true);

      Animated.parallel([
        Animated.timing(translateY, {
          toValue: 0, // slide lên đúng vị trí
          duration: 250,
          easing: Easing.out(Easing.cubic),
          useNativeDriver: true,
        }),
        Animated.timing(opacity, {
          toValue: 1,
          duration: 200,
          useNativeDriver: true,
        }),
      ]).start();
    } else {
      Animated.parallel([
        Animated.timing(translateY, {
          toValue: 50, // slide xuống
          duration: 200,
          easing: Easing.in(Easing.cubic),
          useNativeDriver: true,
        }),
        Animated.timing(opacity, {
          toValue: 0,
          duration: 150,
          useNativeDriver: true,
        }),
      ]).start(() => {
        setShouldRender(false); // unmount sau khi đóng xong
      });
    }
  }, [visible, translateY, opacity]);

  if (!shouldRender) return null;

  return (
    <View style={styles.overlay}>
      <Animated.View
        style={[
          styles.content,
          {
            transform: [{ translateY }],
            opacity,
          },
        ]}
      >
        <View style={styles.headerContainer}>
          <Text style={styles.headerContainerTitle}>
            {contract?.companyName} {t('CONTRACT')}
          </Text>
          <Button
            style={styles.headerContainerCloseButton}
            variant="ghost"
            onPress={handleClose}
          >
            <SvgXml
              xml={IconClose(colors.white)}
              width={SCREEN_WIDTH * 0.05}
              height={SCREEN_WIDTH * 0.05}
            />
          </Button>
        </View>

        {error && (
          <View style={styles.pdfContainerError}>
            <Text style={styles.pdfContainerErrorText}>
              {t('PDF_NOT_FOUND')}
            </Text>
          </View>
        )}

        {contract?.pdfFile?.urlShow && !error && (
          <View style={styles.pdfContainer}>
            <PdfView
              style={styles.pdfView}
              trustAllCerts={false}
              source={{
                uri: contract?.pdfFile?.urlShow,
              }}
              spacing={0}
              scale={1.0}
              onError={() => {
                setError(true);
              }}
            />
          </View>
        )}
      </Animated.View>
    </View>
  );
};

export default Pdf;
